/*-------  VARIABLES -------*/
/*------END VARIABLES -------*/
/*----- MIXINS ----*/
/*-----END MIXINS-----*/
/*------ Common ----*/
* {
  box-sizing: border-box;
}

body {
  font-family: 'ubuntu', sans-serif;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.42;
  -webkit-font-smoothing: antialiased;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  color: #414141;
  font-weight: normal;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

p {
  margin: 0;
  padding: 0;
}

.container {
  width: 940px;
  margin: 0 auto;
  position: relative;
  height: 100%;
}

.container:after {
  content: "";
  display: table;
  clear: both;
}

.content {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.title {
  font-family: 'gagalin';
  font-size: 72px;
}

.title_yellow {
  color: #f9b43b;
}

.title_grey {
  color: #2f3234;
}

.title_white {
  color: #fff;
}

.btn {
  color: #fff;
  border-radius: 5px;
  padding: 10px 15px;
  font-family: 'ubuntu', sans-serif;
  font-size: 13px;
  font-weight: 500;
  text-transform: uppercase;
  display: inline-block;
  margin-top: -15px;
  vertical-align: middle;
}

.btn_order {
  background-color: #e35028;
  margin-left: 30px;
}

.btn_order:hover {
  background-color: #c1401e;
}

.btn_reviews {
  background-color: #2f3234;
}

.btn_reviews:hover {
  background-color: #1f2122;
}

.section {
  height: 100vh;
  min-height: 650px;
  background-size: cover;
  background-position: center center;
  position: relative;
  overflow: hidden;
}

/*------END Common ----*/
/*-----SCREEN 1-----*/
.one {
  background-image: url(../img/screen-1.jpg);
}

.top__menu {
  position: absolute;
  top: 0;
  width: 100%;
  margin-top: 15px;
}

.top__menu:after {
  content: "";
  display: table;
  clear: both;
}

.top__menu_left {
  float: left;
}

.top__menu_right {
  float: right;
  margin-top: 20px;
}

.top__menu__list {
  color: #fff;
  display: inline-block;
}

.top__menu__list_item {
  float: left;
  font-size: 13px;
  font-weight: 500;
  text-transform: uppercase;
  margin-right: 30px;
  list-style-type: disc;
}

.top__menu__list_item:first-child {
  list-style-type: none;
}

.top__menu__list_item:last-child {
  margin-right: 0;
}

.top__menu__link {
  color: #fff;
  text-transform: uppercase;
}

.top__menu__link:hover {
  text-decoration: underline;
}

.first {
  text-align: center;
}

.first__img {
  display: inline-block;
  width: 49%;
  vertical-align: middle;
}

.first__text {
  display: inline-block;
  width: 49%;
  vertical-align: middle;
  text-align: left;
}

.first__desc {
  font-family: 'lifehack', sans-serif;
  color: #fff;
  font-size: 65px;
}

.first__sparrow {
  position: absolute;
  bottom: 5%;
  left: 50%;
}

/*----END SCREEN 1----*/
/*---SCREEN TWO-----*/
.second {
  background-image: url(../img/screen-2.jpg);
  color: #2f3234;
  text-align: center;
}

.options__list {
  font-size: 0;
  margin-top: 85px;
}

.options__list_item {
  list-style-type: none;
  display: inline-block;
  width: 33%;
  vertical-align: top;
}

.options__list_item_title {
  font-family: 'lifehack', sans-serif;
  font-size: 24px;
  margin-top: 15px;
  font-weight: 300;
}

.options__list_item_text {
  font-size: 14px;
  font-family: 'ubuntu', sans-serif;
  font-weight: 300;
  line-height: 24px;
}

/*----END SCREEN TWO-----*/
/*----SCREEN THREE-----*/
.third {
  background-image: -webkit-linear-gradient(to bottom, #f9b43b 0%, #eb8c38 48%, #f9b43b 74%);
  background-image: -o-linear-gradient(to bottom, #f9b43b 0%, #eb8c38 48%, #f9b43b 74%);
  background-image: linear-gradient(to bottom, #f9b43b 0%, #eb8c38 48%, #f9b43b 74%);
}

.burger_slider:after {
  content: "";
  display: table;
  clear: both;
}

.burger_slider__left {
  float: left;
  width: 470px;
  position: absolute;
  top: 0;
  bottom: 0;
}

.burger_slider__right {
  float: right;
  width: 470px;
}

.burger_slider__img__picture {
  width: 470px;
}

.burger_slider__text {
  color: #fff;
  font-size: 14px;
  font-family: 'ubuntu', sans-serif;
  font-weight: 300;
  line-height: 24px;
}

.burger_slider__cost {
  font-family: 'gagalin', sans-serif;
  font-size: 72px;
  color: #e35028;
  display: inline-block;
}

.burger_slider__currency {
  font-family: 'gagalin', sans-serif;
  font-size: 30px;
  color: #e35028;
  text-transform: uppercase;
  display: inline-block;
}

.burger_slider__composition {
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
  border-radius: 5px;
  background-color: rgba(227, 80, 40, 0.4);
  padding: 12px 22px;
  transition: .5s;
}

.burger_slider__composition:hover {
  background-color: #e35028;
}

.burger_slider__composition:hover .burger_slider__composition__table {
  display: block;
}

.burger_slider__composition__text {
  display: block;
  color: #fff;
  font-size: 14px;
  font-family: 'gagalin', sans-serif;
}

.burger_slider__composition__table {
  display: none;
  position: absolute;
  top: 0;
  left: 90px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.8);
  width: 270px;
}

.burger_slider__composition__kkal {
  position: absolute;
  bottom: 0;
  left: 0;
  font-family: 'gagalin', sans-serif;
  color: #fff;
  text-align: center;
}

.burger_slider__composition__kkal__count {
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.burger_slider__composition__kkal__text {
  font-size: 14px;
  display: block;
}

/*----END SCREEN THREE----*/
/*-----SCREEN FOURTH----*/
.fourth {
  background-image: url(../img/screen-3.jpg);
}

.team {
  float: right;
  width: 50%;
  height: 100%;
  background-color: rgba(47, 50, 52, 0.93);
  padding: 100px 30px 50px 90px;
}

.team-accordion {
  margin-top: 50px;
}

.team__item {
  margin-bottom: 30px;
}

.team__item_active .team__desc {
  display: block;
}

.team__item_active .team__trigger {
  color: #f9b43b;
}

.team__item_active .team__trigger:before {
  -webkit-transform: rotate(-180deg);
  -ms-transform: rotate(-180deg);
  transform: rotate(-180deg);
}

.team__trigger {
  display: block;
  text-decoration: none;
  padding-left: 15px;
  position: relative;
  color: #fff;
  font-family: 'gagalin', sans-serif;
  font-size: 14px;
}

.team__trigger:hover {
  color: #f9b43b;
}

.team__trigger:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 5px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 4px 8px 4px;
  border-color: transparent transparent #f9b33b transparent;
  -webkit-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: .3s;
}

.team__desc {
  margin: 20px 0 0 20px;
  font-size: 0;
  display: none;
}

.team__desc_foto {
  border-radius: 50%;
  text-align: center;
  overflow: hidden;
  background-size: cover;
  background-position: center center;
  width: 100px;
  height: 100px;
  margin-left: 10px;
  display: inline-block;
}

.team__desc__about {
  color: #fff;
  font-family: 'ubuntu', sans-serif;
  font-size: 14px;
  margin-left: 20px;
  display: inline-block;
  max-width: 75%;
  vertical-align: top;
}

.team__desc__about_position {
  font-weight: 600;
}

.team__desc__about_text {
  font-weight: 300;
  margin-top: 20px;
}

/*----END SCREEN FOURTH----*/
/*---SCREEN FIFTH-----*/
.fifth {
  background-image: url(../img/screen-4.jpg);
}

.fifth_left {
  float: left;
  padding: 100px 0 0 100px;
}

.fifth_right {
  float: right;
  color: #fff;
}

.menu-acco {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
}

.menu-acco:after {
  content: "";
  display: table;
  clear: both;
}

.menu-acco__item {
  float: left;
  height: 100%;
}

.menu-acco__item:after {
  content: "";
  display: table;
  clear: both;
}

.menu-acco__item_active .menu-acco__content {
  width: 540px;
}

.menu-acco__item_active .menu-acco__trigger_text {
  color: #f9b43b;
}

.menu-acco__trigger {
  display: block;
  width: 80px;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  position: relative;
  float: left;
}

.menu-acco__trigger_text {
  font-family: 'gagalin', sans-serif;
  font-size: 40px;
  color: #fff;
  position: absolute;
  top: 50%;
  height: 80px;
  margin-top: -40px;
  white-space: nowrap;
  width: 650px;
  left: 50%;
  margin-left: -315px;
  -webkit-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  transform: rotate(-90deg);
  text-align: center;
}

.menu-acco__content {
  background-color: rgba(47, 50, 52, 0.93);
  width: 0;
  color: #fff;
  font-family: 'ubuntu', sans-serif;
  font-size: 14px;
  font-weight: 300;
  float: left;
  height: 100%;
  overflow: hidden;
}

.menu-acco__text {
  width: 540px;
  padding: 60px 30px;
}

/*----END SCREEN FIFTH-----*/
/*----SCREEN SIXTH----*/
.sixth_plate {
  float: left;
  width: 25%;
  height: 50%;
  background-size: cover;
  background-position: center center;
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
}

.sixth_plate:hover .reviews {
  display: block;
}

.reviews {
  display: none;
  font-size: 14px;
  color: #fff;
  padding: 50px 20px;
  background-color: rgba(0, 0, 0, 0.7);
  height: 100%;
}

.reviews__title {
  font-family: 'gagalin', sans-serif;
}

.reviews__text {
  margin: 20px 0 40px 0;
  font-family: 'ubuntu', sans-serif;
  font-weight: 300;
}

/*-----END SCREEN SIXTH---*/
/*----SCREEN SEVENTH-----*/
.seventh {
  background-image: url(../img/screen-6.jpg);
}

.order__label {
  color: #fff;
  display: inline-block;
}

/*-----END SCREEN SEVENTH-----*/
/*-----SCREEN EIGHT-------*/
.yandex-map {
  height: calc(100%-60px);
  width: 100%;
}

.footer {
  height: 60px;
}

/*-----END SCREEN SEVENTH-----*/
